#form-btn {
  float: top;
  color: white;
  text-transform: uppercase;
  cursor: pointer;
  padding: 5px;
  font-family: 'Heebo', Helvetica, Arial, sans-serif;
	font-weight: 500;
	font-size: 10px;
  letter-spacing: 0.05em;
  text-shadow: 0px 0px 1px rgb(40, 191, 146);
  border-radius: 0px;
  background: -webkit-linear-gradient(45deg,#31b88f,teal);
  background: -o-linear-gradient(45deg,#31b88f,teal);
  background: linear-gradient(45deg,#31b88f,teal);
  -webkit-box-shadow: .5vw .5vw 0 #b1b3bd;
          box-shadow: .5vw .5vw 0 #b1b3bd;
  -webkit-transition-duration: 0.3s;
  -o-transition-duration: 0.3s;
     transition-duration: 0.3s;
  -webkit-transition-property: transform;
  -webkit-transition-property: -webkit-transform;
  transition-property: -webkit-transform;
  -o-transition-property: transform;
  transition-property: transform;
  transition-property: transform, -webkit-transform;
}

#form-btn:hover {
    -webkit-box-shadow: 0.2vw 0.2vw 0 #9ea1b8;
            box-shadow: 0.2vw 0.2vw 0 #9ea1b8;
    -webkit-transform: scale(1.1);
    -ms-transform: scale(1.1);
        transform: scale(1.1);
}




